.hero {
  background-color: $bg-color-header;
  // max-width: 1280px;
  max-width: 320px;
  height: 502px;
  display: flex;
  margin: 0 auto;
  align-items: center;

  // @media screen and (max-width: 767px) {
  //   max-width: 320px;
  //   align-items: center;
  // }

  // @media screen and (min-width: 768px) and (max-width: 1199px) {
  //   max-width: 768px;
  //   height: 399px;
  //   align-items: flex-end;
  // }

  // @media screen and (min-width: 1200px) {
  //   height: 665px;
  //   align-items: flex-end;
  // }
}

.ice-cream {
  background-image: url(../images/ice-cream-hero.png);
  background-size: 171px 410px;
  background-position: 80% 100%;
  background-repeat: no-repeat;

  // @media screen and (min-width: 768px) and (max-width: 1199px) {
  //   background-size: 143px 336px;
  //   background-position: 60% 100%;
  // }

  // @media screen and (min-width: 1200px) {
  //   background-size: 236px 557px;
  //   background-position: 60% 100%;
  // }
}

@media (min-device-pixel-ratio: 2),
(-webkit-min-device-pixel-ratio: 2),
(min-resolution: 192dpi),
(min-resolution: 2dppx) {
.ice-cream {
  background-image: url(../images/ice-cream-hero2x.png);
}
}

.hero__title {
  font-family: $accent-font;
  font-weight: 400;
  font-size: 26px;
  line-height: 1.15;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  color: $primary-white-color;

  margin-top: 30px;
  // @media screen and (min-width: 768px) and (max-width: 1199px) {
  //   font-size: 22px;
  //   line-height: 1.14;
  // }

  // @media screen and (min-width: 1200px) {
  //   font-size: 38px;
  //   line-height: 1.16;
  // }
}

.hero__title--pink {
  font-family: $accent-font;
  font-weight: 400;
  font-size: 48px;
  line-height: 0.96;
  text-transform: uppercase;

  color: $accent-color;

  // @media screen and (min-width: 768px) and (max-width: 1199px) {
  //   font-size: 34px;
  //   line-height: 0.9;
  // }

  // @media screen and (min-width: 1200px) {
  //   font-size: 58px;
  //   line-height: 0.9;
  // }
}

.hero__button{
  font-family: inherit;
  font-style: normal;
  font-weight: 400;
  font-size: 8px;
  line-height: 1.25;
  border-radius: 20px;
  border: 1px solid transparent;

  display: inline-block;
  align-items: center;
  text-align: center;
  cursor: pointer;

  padding: 6px 12px;
  margin-top: 19px;

//  @media screen and (min-width: 1200px) {
//   font-size: 14px;
//   line-height: 1.29;
//   padding: 10px 20px;
//  }
}

.hero__button--pink {
  background-color: $accent-color;
  color: $primary-white-color;

  &:hover, &:focus {
    background-color: #C7133E;
  }
}

.hero__button--white {
  background-color: $primary-white-color;
  color: $accent-color;

  margin-left: 8px;

  // @media screen and (min-width: 1200px) {
  //   margin-left: 15px;
  // }

  &:hover, &:focus {
    background-color: #ECEBEB;
  }
}

